import ttStatusBar from "../../components/tt-status-bar/tt-status-bar"
import Class from '../class/class.vue'
import Hot from '../hot/hot.vue'
import Teacher from '../teacher/teacher.vue'
import Student from '../student/student.vue'
export default {
	components:{
		ttStatusBar,
		Class,
		Hot,
		Teacher,
		Student
	},
	data() {
		let statusBarHeight = uni.getSystemInfoSync().statusBarHeight + 'px'
		return {
			statusBarHeight: statusBarHeight,
			date: new Date(),
			week: '',
			tabIndex: 0,
			scrollInto: "",
			listHeight: '',
			current: '',
			tabBars:[{
				name: '课程',
				id: 'class'
			},{
				name: '热点',
				id: 'hot',
			},{
				name: '家教',
				id: 'teacher'
			},
			{
				name: '学生',
				id: 'student'
			}]
			
		}
	},
	created() {
	},
	mounted() {
		this.getInit()
	},
	methods: {
		ontabtap(e){
			let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.current = index
			this.switchTab(index);
		},
		switchTab(index) {
		  
		    if (this.tabIndex === index) {
		        return;
		    }
		    this.tabIndex = index;
		},
		ontabchange(e){
			let index = e.target.current || e.detail.current;
			this.switchTab(index);
		},
		getInit(){
			let now = new Date();
			let day = now.getDay();
			let weeks = new Array("星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六");
			this.week = weeks[day]
			uni.getSystemInfo({
			    success: (res)=> {
					const query = uni.createSelectorQuery().in(this);
					query.select('.scroll-h').boundingClientRect(data => {
					  this.listHeight  = res.windowHeight - data.height + 'px'
					}).exec();
					
			    }
			});
		}
	},
	filters:{
		formatDate: (value)=> {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			return `${y}-${MM}-${d}`;
		}
	}
}
